import {IPasswordConfig} from '../app/account/settings/password/interface';
import {getConfig} from './httpClient';

export const createAccountSettingsServices = (fetchRequest: any) => ({
  async getAllConfig() {
    try {
      const endpoint = '/services/uaa/api/security-configs/all';
      const data = await fetchRequest({
        url: getConfig().BASE_URL_EACCOUNT + endpoint,
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        },
      });
      return Promise.resolve(data?.data);
    } catch (error) {
      console.log('getAllConfig: ', error);
      return Promise.reject(error);
    }
  },

  async editSecurityPasswordConfig(
    body: IPasswordConfig[],
  ): Promise<IPasswordConfig[]> {
    try {
      const endpoint = '/services/uaa/api/security-configs/save';
      const data = await fetchRequest({
        url: getConfig().BASE_URL_EACCOUNT + endpoint,
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({data: body}),
      });
      return Promise.resolve(data?.data);
    } catch (error) {
      console.log('editSecurityPasswordConfig: ', error);
      return Promise.reject(error);
    }
  },
});
