import {createAccountInfoServices} from '../../services/accountInfo.services';
import {createAccountSettingsServices} from '../../services/settings.services';
import {useFetchRequest} from '../useFetch';

export const useAccountInfoServices = () => {
  const fetchRequest = useFetchRequest();
  return createAccountInfoServices(fetchRequest);
};

export const useAccountSettingService = () => {
  const fetchRequest = useFetchRequest();
  return createAccountSettingsServices(fetchRequest);
};
