import {getConfig} from './httpClient';

export const createAccountInfoServices = (fetchRequest: any) => ({
  async deleteAvatar() {
    try {
      const endpoint = '/services/uaa/api/account/avatar';
      const data = await fetchRequest({
        url: getConfig().BASE_URL_EACCOUNT + endpoint,
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json',
        },
        typeOfResponse: '',
      });
      return Promise.resolve(data?.data);
    } catch (error) {
      console.log('deleteAvatar: ', error);
      return Promise.reject(error);
    }
  },

  async updateAccountAvatar(body: FormData) {
    try {
      const endpoint = '/services/uaa/api/account/avatar-zoom';
      const data = await fetchRequest({
        url: getConfig().BASE_URL_EACCOUNT + endpoint,
        method: 'POST',
        headers: {
          'Content-Type': 'multipart/form-data',
        },
        body,
      });
      return Promise.resolve(data?.data);
    } catch (error) {
      console.log('updateAccountAvatar: ', error);
      return Promise.reject(error);
    }
  },

  async updateAccount(body: any) {
    try {
      const endpoint = '/services/uaa/api/account';
      const data = await fetchRequest({
        url: getConfig().BASE_URL_EACCOUNT + endpoint,
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(body),
      });
      return Promise.resolve(data?.data);
    } catch (error) {
      console.log('updateAccount: ', error);
      return Promise.reject(error);
    }
  },

  async getSecuriryPasswordRules(): Promise<any> {
    try {
      const endpoint = '/services/uaa/api/security-configs/all';
      const data = await fetchRequest({
        url: getConfig().BASE_URL_EACCOUNT + endpoint,
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        },
      });
      return Promise.resolve(data?.data);
    } catch (error) {
      console.log('getSecuriryPasswordRules: ', error);
      return Promise.reject(error);
    }
  },

  async changePassword(body: any): Promise<any> {
    try {
      const endpoint = '/services/uaa/api/account/change-password';
      const data = await fetchRequest({
        url: getConfig().BASE_URL_EACCOUNT + endpoint,
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(body),
        getValueErr: true,
        typeOfResponse: '',
      });
      return Promise.resolve(data);
    } catch (error: any) {
      console.log('changePassword: ', error);
      return Promise.reject(error);
    }
  },

  async getOrganizationDetail() {
    try {
      const endpoint = '/services/uaa/api/organization-detail-profile';
      const data = await fetchRequest({
        url: getConfig().BASE_URL_EACCOUNT + endpoint,
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        },
      });
      return Promise.resolve(data?.data);
    } catch (error) {
      console.log('getOrganizationDetail: ', error);
      return Promise.reject(error);
    }
  },

  async getOrgLegalInProfileAuth(step: string) {
    try {
      const endpoint = `/services/uaa/api/profile-authentication/legal?step=${step}`;
      const data = await fetchRequest({
        url: getConfig().BASE_URL_EACCOUNT + endpoint,
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        },
      });
      return Promise.resolve(data?.data);
    } catch (error) {
      console.log('getOrgLegalInProfileAuth: ', error);
      return Promise.reject(error);
    }
  },

  async getPersonalProfileEkyc(userId: any) {
    try {
      const endpoint =
        '/services/uaa/api/personal-kyc-detail-profiles-eaccount-by-userId/' +
        userId;
      const data = await fetchRequest({
        url: getConfig().BASE_URL_EACCOUNT + endpoint,
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        },
      });
      return Promise.resolve(data?.data);
    } catch (error) {
      console.log('getPersonalProfileEkyc: ', error);
      return Promise.reject(error);
    }
  },
});
